	module.exports = {"data":{"markdownRemark":{"headings":[],"html":"<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 1500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 56.25%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"kuzyk lauren rfs pub02 02\"\n        title=\"\"\n        src=\"/the-next-website/static/kuzyk_lauren_rfs_pub02_02-89bc41ca56f99c4c45ebbe5c4ed43d97-35578.jpg\"\n        srcset=\"/the-next-website/static/kuzyk_lauren_rfs_pub02_02-89bc41ca56f99c4c45ebbe5c4ed43d97-21994.jpg 375w,\n/the-next-website/static/kuzyk_lauren_rfs_pub02_02-89bc41ca56f99c4c45ebbe5c4ed43d97-f7c9d.jpg 750w,\n/the-next-website/static/kuzyk_lauren_rfs_pub02_02-89bc41ca56f99c4c45ebbe5c4ed43d97-35578.jpg 1500w,\n/the-next-website/static/kuzyk_lauren_rfs_pub02_02-89bc41ca56f99c4c45ebbe5c4ed43d97-5ea4e.jpg 2000w\"\n        sizes=\"(max-width: 1500px) 100vw, 1500px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>Muttual is a community focused group at the core that fosters local and symbiotic relationships. Through facilitating dog walking services, this company makes matches within a network of dogs owners and walkers to culture a trusting and supportive neighbourhood collective.</p>\n<p>The name Muttual plays off the canine subject and the company interest in mutual benefit.</p>\n<p>The logo exists as a word mark, icon or combination of the two. It can be truncated or expanded through three versions, the short form, long form, and addition of the slogan. The roots of the icon comes in multiple variations where the same shapes are rotated/rearranged to depict a variety of dog breeds for acknowledging diversity. There are generally 4 shape types that derive from square and circle units. The logical make up behind these forms make for a recognizable visual language.</p>\n<p>Developing a flexible visual language allows interaction with the brand. By offering a toolbox of shapes and guidelines about how to position them, Muttual encourages creativity and invites the user to be a piece of the Muttual community rather just a user.</p>\n<p>A comprehensive look into the brand, it’s identity system and application can be found in the Muttual Brand Guidelines Book. View it on issuu here.</p>","frontmatter":{"title":"Muttual","graduate":"Lauren Kuzyk","category":"Brand Identity","category2":null,"category3":null,"image":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":1.5,"src":"/the-next-website/static/kuzyk_muttual-14b65871f3f165765239199a91ae5b56-0f681.png","srcSet":"/the-next-website/static/kuzyk_muttual-14b65871f3f165765239199a91ae5b56-c1674.png 375w,\n/the-next-website/static/kuzyk_muttual-14b65871f3f165765239199a91ae5b56-34f47.png 750w,\n/the-next-website/static/kuzyk_muttual-14b65871f3f165765239199a91ae5b56-0f681.png 1500w,\n/the-next-website/static/kuzyk_muttual-14b65871f3f165765239199a91ae5b56-8123c.png 2000w","sizes":"(max-width: 1500px) 100vw, 1500px"}}}}}},"pathContext":{"slug":"/work/muttual/","name":"/Muttual/"}}

